import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import Zoomy from 'react-zoomy';
import {ImageButton, Index, ImageContainer, Title } from './Projects.style';
import { Container, Relative, Flex } from 'theme/grid';
import { A } from 'theme/types';
import ParallaxImage from 'react-image-parallax2';
import { hot } from 'react-hot-loader';

class Projects extends Component {
    // constructor(props) {
    //     super(props);
    // }

    render() {
        return (
            <Container>
            	<Title>Open-source Projects</Title>
            	<Relative marginBottom="50px" marginTop="100px">
            		<Index>
	            		<h1>01</h1>
	            	</Index>	
	            	<h1>npm install --save <A href="#">react-zoomy</A></h1>
            	</Relative>
            	<Flex justify={'center'} marginBottom="5em">
            		<Zoomy
					  imageUrl= {require('assets/forest.png')}
					  renderThumbnail={({ showImage }) => 
					  <ImageButton onClick={showImage}>
					  	<img src={require('assets/forest_thumbnail.png')} alt=""/>
					  </ImageButton>
					}
					  scale={[1.1, 1.1]}
					  imageProps={{
					    style: {
					      width: '100vw',
					      height: 'auto'
					    }
					  }}
					/>
            	</Flex>
            	<Relative marginBottom="50px">
            		<Index>
	            		<h1>02</h1>
	            	</Index>	
	            	<h1>npm install --save <A href="#">react-image-parallax2</A></h1>
            	</Relative>
            	<Flex justify={'center'}>
	            	<ImageContainer>
	            		<ParallaxImage 
	            		reduceHeight={1/3}
	            		src={require('assets/yoonu.jpg')}/>
	            	</ImageContainer>
            	</Flex>
            
            	<br/>
            	<br/>
            	<br/>
            	<br/>
            	<br/>
            	<br/>
            	<br/>
            </Container>
        );
    }
}

export default hot(module)(Projects);


