import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Waypoint from 'react-waypoint';

const propTypes = {
	number: PropTypes.number
}

const defaultProps = {
	number: -1
}

class WhenInView extends Component {
 	constructor(props){
 		super(props);
 		this.state = {
 			isInView: false
 		}
 		this.onEnter = this.onEnter.bind(this);
 	}
 	// when scrolling down
 	onEnter({ previousPosition }){
		if(previousPosition === Waypoint.below){
			this.setState({
				isInView: true
			})
		}
 	}
    render() {
        return (
            <div>
            	<Waypoint onEnter={this.onEnter}></Waypoint>
            	{ this.props.children({ isInView: this.state.isInView })}
            </div>
        );
    }
}

WhenInView.propTypes = propTypes;
WhenInView.defaultProps = defaultProps;

export default WhenInView;


