import { css } from 'styled-components';

const sizes = {
	phone: 376,
	tablet: 768,
	desktop: 992,
	giant: 1170
};

// Object.keys(sizes) : ['phone', 'tablet', 'desktop', 'giant']
// reduce 함수 : return 된 결과값을 다시 finalMedia라는 입력으로 넣음 
// size 는 Object.keys(sizes)의 현재 요소

/* 결과적으로 아래와 같다
	media = {
		phone: function(...args){},
		tablet: function(...args){},
		desktop: function(...args){},
		giant: function(...args){}
	}
*/
const media = Object.keys(sizes).reduce((finalMedia, size)=>{
	return {
		...finalMedia,
		[size]: function(...args){
			return css`
				@media(max-width: ${sizes[size]}px) {
					${css(...args)}
				}
			`;
		}
	}
}, {})


